// Types for the conference schedule data

export interface SocialLinks {
  twitter?: string;
  linkedin?: string;
  github?: string;
  website?: string;
}

export interface CompanyData {
  data: {
    attributes: {
      name: string;
    };
  } | null;
}

export interface ProfilePhoto {
  data: {
    attributes: {
      url: string;
    };
  } | null;
}

export interface PresenterAttributes {
  name: string;
  tagline: string;
  company: CompanyData;
  profilePhoto: ProfilePhoto;
  socialLinks?: SocialLinks;
}

export interface Presenter {
  attributes: PresenterAttributes;
}

export interface Session {
  id: string;
  title: string;
  about: string;
  since: string;
  endsAt: string;
  trackName: string;
  room: string;
  roomOther: string;
  presenters: Presenter[];
  slug: string;
  displayOrder: number;
  recordingLink: string | null;
  type: 'SESSION' | 'WORKSHOP' | 'SERVICE' | 'OTHER';
  isPlenumSession: boolean;
}

// Helper functions

export function parseDateTime(isoString: string): Date {
  return new Date(isoString);
}

export function formatTime(date: Date): string {
  return date.toLocaleTimeString('en-US', {
    hour: 'numeric',
    minute: '2-digit',
    hour12: true,
  });
}

export function formatTimeRange(start: Date, end: Date): string {
  return `${formatTime(start)} - ${formatTime(end)}`;
}

export function getDurationMinutes(start: Date, end: Date): number {
  return Math.round((end.getTime() - start.getTime()) / (1000 * 60));
}

export function getSessionDate(session: Session): string {
  const date = parseDateTime(session.since);
  return date.toISOString().split('T')[0];
}

export function getSessionsByDay(sessions: Session[], dateStr: string): Session[] {
  return sessions
    .filter(session => getSessionDate(session) === dateStr)
    .sort((a, b) => {
      const timeA = parseDateTime(a.since).getTime();
      const timeB = parseDateTime(b.since).getTime();
      if (timeA !== timeB) return timeA - timeB;
      return a.displayOrder - b.displayOrder;
    });
}

export function groupSessionsByTime(sessions: Session[]): Map<string, Session[]> {
  const groups = new Map<string, Session[]>();
  
  for (const session of sessions) {
    const startTime = formatTime(parseDateTime(session.since));
    const existing = groups.get(startTime) || [];
    existing.push(session);
    groups.set(startTime, existing);
  }
  
  return groups;
}

export function getUniqueTracks(sessions: Session[]): string[] {
  const tracks = new Set<string>();
  sessions.forEach(s => tracks.add(s.trackName));
  return Array.from(tracks).sort();
}

export function getUniqueRooms(sessions: Session[]): string[] {
  const rooms = new Set<string>();
  sessions.forEach(s => rooms.add(s.room));
  return Array.from(rooms).sort();
}

export function isSessionNow(session: Session, now: Date = new Date()): boolean {
  const start = parseDateTime(session.since);
  const end = parseDateTime(session.endsAt);
  return now >= start && now <= end;
}

export function isSessionUpcoming(session: Session, now: Date = new Date(), withinMinutes: number = 30): boolean {
  const start = parseDateTime(session.since);
  const msUntilStart = start.getTime() - now.getTime();
  return msUntilStart > 0 && msUntilStart <= withinMinutes * 60 * 1000;
}

export function getTrackColor(trackName: string): string {
  const colors: Record<string, string> = {
    'Keynote': 'var(--color-track-keynote)',
    'AI Leadership': 'var(--color-track-leadership)',
    'AI Engineering': 'var(--color-track-engineering)',
    'Workshop': 'var(--color-track-workshop)',
    'Expo': 'var(--color-track-expo)',
    'Service': 'var(--color-track-service)',
    'Plenary': 'var(--color-track-keynote)',
  };
  return colors[trackName] || 'var(--color-text-muted)';
}

export function getSessionTypeLabel(type: Session['type']): string {
  const labels: Record<Session['type'], string> = {
    'SESSION': 'Talk',
    'WORKSHOP': 'Workshop',
    'SERVICE': 'Break',
    'OTHER': 'Event',
  };
  return labels[type];
}

