'use client';

import { cn } from '@/app/lib/cn';
import { Card } from '@/app/components/ui/card';
import { Badge } from '@/app/components/ui/badge';
import { SpeakerAvatarGroup } from './SpeakerAvatar';
import { 
  type Session, 
  parseDateTime, 
  formatTimeRange, 
  getDurationMinutes,
  getTrackColor,
  getSessionTypeLabel 
} from '@/app/lib/schedule-data';
import { Heart, Clock, MapPin, ChevronRight } from 'lucide-react';
import { useState } from 'react';

interface SessionCardProps {
  session: Session;
  isBookmarked: boolean;
  onToggleBookmark: (sessionId: string) => void;
  variant?: 'default' | 'compact';
}

export function SessionCard({ 
  session, 
  isBookmarked, 
  onToggleBookmark,
  variant = 'default' 
}: SessionCardProps) {
  const [isExpanded, setIsExpanded] = useState(false);
  
  const startTime = parseDateTime(session.since);
  const endTime = parseDateTime(session.endsAt);
  const duration = getDurationMinutes(startTime, endTime);
  const trackColor = getTrackColor(session.trackName);

  const isServiceSession = session.type === 'SERVICE';

  if (isServiceSession && variant === 'default') {
    // Simplified card for service sessions (breaks, lunch, etc.)
    return (
      <div 
        id={`session-${session.id}`}
        className="flex items-center gap-3 px-4 py-3 bg-[var(--color-surface)]/50 rounded-lg border border-[var(--color-border-subtle)]"
      >
        <div className="w-1 h-8 rounded-full bg-[var(--color-text-muted)]/30" />
        <div className="flex-1 min-w-0">
          <p className="text-sm font-medium text-[var(--color-text-secondary)]">
            {session.title}
          </p>
          <p className="text-xs text-[var(--color-text-muted)]">
            {formatTimeRange(startTime, endTime)} â€¢ {session.room}
          </p>
        </div>
      </div>
    );
  }

  return (
    <Card 
      id={`session-${session.id}`}
      className={cn(
        'group relative overflow-hidden transition-all duration-200',
        'hover:shadow-lg hover:shadow-black/20',
        variant === 'compact' && 'p-3'
      )}
    >
      {/* Track Color Indicator */}
      <div 
        className="absolute left-0 top-0 bottom-0 w-1"
        style={{ backgroundColor: trackColor }}
      />

      <div className={cn(
        'pl-4',
        variant === 'default' ? 'p-4 md:p-5' : 'p-3'
      )}>
        {/* Header with badges and bookmark */}
        <div className="flex items-start justify-between gap-2 mb-2">
          <div className="flex flex-wrap items-center gap-2">
            <Badge variant="track" trackColor={trackColor}>
              {session.trackName}
            </Badge>
            {session.type === 'WORKSHOP' && (
              <Badge variant="secondary">Workshop</Badge>
            )}
            {session.isPlenumSession && (
              <Badge variant="outline">Plenary</Badge>
            )}
          </div>
          
          <button
            onClick={(e) => {
              e.stopPropagation();
              onToggleBookmark(session.id);
            }}
            className={cn(
              'p-1.5 rounded-lg transition-all duration-200',
              'hover:bg-[var(--color-surface-elevated)]',
              'focus:outline-none focus-visible:ring-2 focus-visible:ring-[var(--color-primary)]',
              isBookmarked && 'text-[var(--color-bookmark)]'
            )}
            aria-label={isBookmarked ? 'Remove bookmark' : 'Add bookmark'}
          >
            <Heart className={cn('w-5 h-5', isBookmarked && 'fill-current')} />
          </button>
        </div>

        {/* Title */}
        <h3 className={cn(
          'font-semibold text-[var(--color-text-primary)] mb-2 leading-tight',
          variant === 'default' ? 'text-base md:text-lg' : 'text-sm'
        )}>
          {session.title}
        </h3>

        {/* Meta info */}
        <div className="flex flex-wrap items-center gap-x-4 gap-y-1 text-sm text-[var(--color-text-muted)] mb-3">
          <div className="flex items-center gap-1.5">
            <Clock className="w-4 h-4" />
            <span>{formatTimeRange(startTime, endTime)}</span>
            <span className="text-[var(--color-text-muted)]/60">({duration}min)</span>
          </div>
          <div className="flex items-center gap-1.5">
            <MapPin className="w-4 h-4" />
            <span className="truncate max-w-[150px]">{session.room}</span>
          </div>
        </div>

        {/* Speakers */}
        {session.presenters.length > 0 && (
          <div className="mb-3">
            <SpeakerAvatarGroup presenters={session.presenters} />
          </div>
        )}

        {/* Description (expandable) */}
        {session.about && variant === 'default' && (
          <div className="mt-3">
            <p className={cn(
              'text-sm text-[var(--color-text-secondary)] leading-relaxed',
              !isExpanded && 'line-clamp-2'
            )}>
              {session.about}
            </p>
            {session.about.length > 150 && (
              <button
                onClick={() => setIsExpanded(!isExpanded)}
                className="mt-1 text-xs text-[var(--color-primary)] hover:underline flex items-center gap-1"
              >
                {isExpanded ? 'Show less' : 'Read more'}
                <ChevronRight className={cn(
                  'w-3 h-3 transition-transform',
                  isExpanded && 'rotate-90'
                )} />
              </button>
            )}
          </div>
        )}
      </div>
    </Card>
  );
}

