'use client';

import { cn } from '@/app/lib/cn';

interface Day {
  date: string;
  label: string;
  fullLabel: string;
}

interface DayNavigatorProps {
  days: Day[];
  activeDay: string;
  onDayChange: (date: string) => void;
}

export function DayNavigator({ days, activeDay, onDayChange }: DayNavigatorProps) {
  return (
    <nav className="sticky top-[73px] z-40 bg-[var(--color-background)]/95 backdrop-blur-lg border-b border-[var(--color-border)]">
      <div className="max-w-7xl mx-auto px-4">
        <div className="flex items-center gap-1 py-2 overflow-x-auto scrollbar-hide">
          {days.map((day) => {
            const isActive = activeDay === day.date;
            return (
              <button
                key={day.date}
                onClick={() => onDayChange(day.date)}
                className={cn(
                  'relative flex-shrink-0 px-4 py-2 rounded-lg text-sm font-medium transition-all duration-200',
                  'focus:outline-none focus-visible:ring-2 focus-visible:ring-[var(--color-primary)] focus-visible:ring-offset-2 focus-visible:ring-offset-[var(--color-background)]',
                  isActive
                    ? 'text-[var(--color-text-primary)]'
                    : 'text-[var(--color-text-muted)] hover:text-[var(--color-text-secondary)] hover:bg-[var(--color-surface)]'
                )}
              >
                {/* Mobile: short label */}
                <span className="md:hidden">{day.label}</span>
                {/* Desktop: full label */}
                <span className="hidden md:inline">{day.fullLabel}</span>
                
                {/* Active indicator */}
                {isActive && (
                  <span 
                    className="absolute bottom-0 left-1/2 -translate-x-1/2 w-8 h-0.5 bg-[var(--color-primary)] rounded-full"
                    style={{ animation: 'slideIn 0.2s ease-out' }}
                  />
                )}
              </button>
            );
          })}
        </div>
      </div>
      
      <style jsx>{`
        @keyframes slideIn {
          from {
            transform: translateX(-50%) scaleX(0);
          }
          to {
            transform: translateX(-50%) scaleX(1);
          }
        }
        .scrollbar-hide {
          -ms-overflow-style: none;
          scrollbar-width: none;
        }
        .scrollbar-hide::-webkit-scrollbar {
          display: none;
        }
      `}</style>
    </nav>
  );
}

