'use client';

import { useMemo } from 'react';
import { SessionCard } from './SessionCard';
import { type Session, groupSessionsByTime, parseDateTime, formatTime } from '@/app/lib/schedule-data';

interface ListViewProps {
  sessions: Session[];
  isBookmarked: (sessionId: string) => boolean;
  onToggleBookmark: (sessionId: string) => void;
}

export function ListView({ sessions, isBookmarked, onToggleBookmark }: ListViewProps) {
  const groupedSessions = useMemo(() => {
    const groups = groupSessionsByTime(sessions);
    // Sort by time
    return Array.from(groups.entries()).sort((a, b) => {
      const timeA = sessions.find(s => formatTime(parseDateTime(s.since)) === a[0]);
      const timeB = sessions.find(s => formatTime(parseDateTime(s.since)) === b[0]);
      if (!timeA || !timeB) return 0;
      return parseDateTime(timeA.since).getTime() - parseDateTime(timeB.since).getTime();
    });
  }, [sessions]);

  return (
    <div className="py-6 space-y-8 stagger-children">
      {groupedSessions.map(([timeSlot, slotSessions]) => (
        <section key={timeSlot}>
          {/* Time Header */}
          <div className="sticky top-[185px] z-20 mb-4">
            <div className="inline-flex items-center gap-2 px-3 py-1.5 bg-[var(--color-surface-elevated)] rounded-full border border-[var(--color-border)] shadow-lg shadow-black/10">
              <div className="w-2 h-2 rounded-full bg-[var(--color-primary)] animate-pulse-soft" />
              <span className="text-sm font-semibold text-[var(--color-text-primary)]">
                {timeSlot}
              </span>
              <span className="text-xs text-[var(--color-text-muted)]">
                {slotSessions.length} session{slotSessions.length > 1 ? 's' : ''}
              </span>
            </div>
          </div>

          {/* Sessions Grid */}
          <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-1 xl:grid-cols-2">
            {slotSessions.map((session) => (
              <SessionCard
                key={session.id}
                session={session}
                isBookmarked={isBookmarked(session.id)}
                onToggleBookmark={onToggleBookmark}
              />
            ))}
          </div>
        </section>
      ))}
    </div>
  );
}

