'use client';

import { useState, useMemo } from 'react';
import { DayNavigator } from './components/schedule/DayNavigator';
import { ViewToggle } from './components/schedule/ViewToggle';
import { FilterBar } from './components/schedule/FilterBar';
import { ListView } from './components/schedule/ListView';
import { TimelineView } from './components/schedule/TimelineView';
import { NowNextBanner } from './components/schedule/NowNextBanner';
import { useBookmarks } from './lib/use-bookmarks';
import { getSessionsByDay, getUniqueTracks, getUniqueRooms, type Session } from './lib/schedule-data';
import scheduleData from './data/schedule.json';

const CONFERENCE_DAYS = [
  { date: '2025-11-19', label: 'Wed', fullLabel: 'Wed, Nov 19' },
  { date: '2025-11-20', label: 'Thu', fullLabel: 'Thu, Nov 20' },
  { date: '2025-11-21', label: 'Fri', fullLabel: 'Fri, Nov 21' },
  { date: '2025-11-22', label: 'Sat', fullLabel: 'Sat, Nov 22' },
];

export default function SchedulePage() {
  const [activeDay, setActiveDay] = useState(CONFERENCE_DAYS[1].date); // Default to Thu
  const [viewMode, setViewMode] = useState<'list' | 'timeline'>('list');
  const [selectedTrack, setSelectedTrack] = useState<string>('all');
  const [selectedRoom, setSelectedRoom] = useState<string>('all');
  const [searchQuery, setSearchQuery] = useState('');
  const [showBookmarksOnly, setShowBookmarksOnly] = useState(false);
  
  const { bookmarks, toggleBookmark, isBookmarked } = useBookmarks();

  const sessions = scheduleData as Session[];
  const tracks = useMemo(() => getUniqueTracks(sessions), [sessions]);
  const rooms = useMemo(() => getUniqueRooms(sessions), [sessions]);

  const filteredSessions = useMemo(() => {
    let result = getSessionsByDay(sessions, activeDay);

    // Filter by track
    if (selectedTrack !== 'all') {
      result = result.filter(s => s.trackName === selectedTrack);
    }

    // Filter by room
    if (selectedRoom !== 'all') {
      result = result.filter(s => s.room === selectedRoom);
    }

    // Filter by search query
    if (searchQuery.trim()) {
      const query = searchQuery.toLowerCase();
      result = result.filter(s => 
        s.title.toLowerCase().includes(query) ||
        s.about.toLowerCase().includes(query) ||
        s.presenters.some(p => 
          p.attributes.name.toLowerCase().includes(query) ||
          p.attributes.company?.data?.attributes?.name?.toLowerCase().includes(query)
        )
      );
    }

    // Filter by bookmarks
    if (showBookmarksOnly) {
      result = result.filter(s => isBookmarked(s.id));
    }

    return result;
  }, [sessions, activeDay, selectedTrack, selectedRoom, searchQuery, showBookmarksOnly, isBookmarked]);

  return (
    <div className="min-h-screen flex flex-col">
      {/* Header */}
      <header className="sticky top-0 z-50 bg-[var(--color-surface)]/80 backdrop-blur-xl border-b border-[var(--color-border)]">
        <div className="max-w-7xl mx-auto px-4 py-3">
          <div className="flex items-center justify-between gap-4">
            <div>
              <h1 className="text-lg md:text-xl font-bold text-[var(--color-text-primary)]">
                AI Engineer Code Summit
              </h1>
              <p className="text-xs md:text-sm text-[var(--color-text-muted)]">
                November 19-22, 2025 â€¢ New York City
              </p>
            </div>
            <ViewToggle viewMode={viewMode} onViewModeChange={setViewMode} />
          </div>
        </div>
      </header>

      {/* Day Navigation */}
      <DayNavigator 
        days={CONFERENCE_DAYS}
        activeDay={activeDay}
        onDayChange={setActiveDay}
      />

      {/* Now/Next Banner */}
      <NowNextBanner 
        sessions={filteredSessions}
        onSessionClick={(sessionId) => {
          const element = document.getElementById(`session-${sessionId}`);
          element?.scrollIntoView({ behavior: 'smooth', block: 'center' });
        }}
      />

      {/* Filters */}
      <FilterBar
        tracks={tracks}
        rooms={rooms}
        selectedTrack={selectedTrack}
        selectedRoom={selectedRoom}
        searchQuery={searchQuery}
        showBookmarksOnly={showBookmarksOnly}
        bookmarkCount={bookmarks.length}
        onTrackChange={setSelectedTrack}
        onRoomChange={setSelectedRoom}
        onSearchChange={setSearchQuery}
        onBookmarksOnlyChange={setShowBookmarksOnly}
      />

      {/* Main Content */}
      <main className="flex-1 max-w-7xl mx-auto w-full px-4 pb-8">
        {filteredSessions.length === 0 ? (
          <div className="flex flex-col items-center justify-center py-16 text-center">
            <div className="w-16 h-16 rounded-full bg-[var(--color-surface-elevated)] flex items-center justify-center mb-4">
              <svg className="w-8 h-8 text-[var(--color-text-muted)]" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1.5} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
              </svg>
            </div>
            <h3 className="text-lg font-medium text-[var(--color-text-primary)] mb-2">
              No sessions found
            </h3>
            <p className="text-sm text-[var(--color-text-muted)] max-w-sm">
              Try adjusting your filters or search query to find what you're looking for.
            </p>
          </div>
        ) : viewMode === 'list' ? (
          <ListView 
            sessions={filteredSessions}
            isBookmarked={isBookmarked}
            onToggleBookmark={toggleBookmark}
          />
        ) : (
          <TimelineView 
            sessions={filteredSessions}
            isBookmarked={isBookmarked}
            onToggleBookmark={toggleBookmark}
          />
        )}
      </main>

      {/* Footer */}
      <footer className="border-t border-[var(--color-border)] py-4 text-center text-xs text-[var(--color-text-muted)]">
        <p>AI Engineer Code Summit 2025</p>
      </footer>
    </div>
  );
}

